@font-face {
    font-family: 'Modelica';
    src: url(../../fonts/BwModelicaSS01-Regular.otf);
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    font-family: 'Modelica';
    background-color: black;
    overflow: hidden;
}
.root-container {
    display: flex;
    position: relative;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.image-section {
    height: 100%;
    flex-basis: 50%;
    background-color: #000;
    position: relative;
}
.side-img {
    width: 100%;
    background: url(../../pexels-breston-kenya-5214278.png) no-repeat center;
    background-size: cover;
    height: 100vh;
}
.form-section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 50%;
    background-color: #000;
    padding: 0rem 2rem;
}

.form-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.form-content h3 {
    letter-spacing: 1px;
    color: #fefefe;
    margin-top: 2rem;
    font-weight: 400;
    font-family: 'Modelica';
}
.Form-Login {
    margin: 0rem 3rem;
}
.Form-Login .email {
    position: relative;
    margin-top: 20px;
    margin-bottom: 15px;
}
.email input {
    width: 100%;
    background-color: transparent;
    font-family: 'Modelica';
    color: #ddd;
    font-size: 1.1rem;
}
.Form-Login .email,
.btn-wrapper {
    margin: 2rem 0rem;
}
.btn-wrapper {
    display: flex;
    align-items: center;
    padding-top: 30px;
}
.btn-wrapper button {
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: transparent;
    color: #fefefe;
    width: 165px;
    border: 1px solid #ffae00;
    font-weight: 500;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
    padding: 12px 0px;
    font-family: 'Modelica';
}
.btn-bg {
    background-color: #ffae00 !important;
}
.ml button {
    margin-left: 4rem;
}
:focus {
    outline: none;
}
.effect-1 {
    border: 0;
    border-bottom: 1px solid #ccc;
    width: 100%;
    height: 30px;
    padding-right: 1px;
}
.effect-1 ~ .focus-border {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
    background-color: #ffae00;
    transition: 0.5s;
}
.effect-1:focus ~ .focus-border {
    width: 100%;
    transition: 0.4s;
}
.effect-1:focus {
    border-bottom: none;
}

.remember-wrapper {
    color: #ffae00;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    font-family: 'Modelica';
}
.remember-wrapper p {
    font-family: 'Modelica';
}
.remember-wrapper a {
    color: #ffae00;
    text-decoration: none;
    font-family: 'Modelica';
}
.remember-wrapper a:hover {
    text-decoration: underline;
}

.text-primary-custom {
    color: rgb(216, 216, 216) !important;
}
.table-text-color {
    color: rgba(221, 221, 221, 0.562) !important;
}
.badge-color {
    background-color: #ddd !important;
}
